* {
  margin: 0px;
}

h1 {
  font-size: 35px;
  font-style: italic;
  font-weight: 400;
  line-height: 36px;
  letter-spacing: -0.03em;
  text-align: left;
  padding-bottom: 32px;
}

h2 {
  font-size: 16px;
  font-weight: 500;
  line-height: 19.2px;
  letter-spacing: -0.01em;
  text-align: left;
  padding-bottom: 16px;
}

h3 {
  font-size: 12px;
  font-weight: 600;
  line-height: 12px;
  letter-spacing: -0.01em;
  text-align: left;
  padding: 0 0 10px 6px;
}

h5 {
  /* font-family: Clash Display; */
  font-size: 12px;
  font-weight: 500;
  line-height: 10.8px;
  letter-spacing: -0.01em;
  text-align: left;
  /*padding-bottom: 392px;*/
  color: #A3A3A3;
}

.mentor_tag {
  width: 28px;
  height: 6px;
  padding: 4px 8px 4px 8px;
  /* gap: 10px; */
  border-radius: 14px;
  /* background: #e6ea0a; */
  background: radial-gradient(circle at center, #FFF6A5 0%, #FFD43B 100%);
  font-family: Clash Display;
  font-size: 6px;
  font-weight: 600;
  line-height: 5.4px;
  /* position: absolute; */
  margin: 109px -57px;
}

